.contacts__wrapper {
  margin: 0 110px 0 110px;
}

.contacts__title {
  font-family: Oswald;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
}

.contacts__list {
  display: flex;
  margin: 0;
  padding: 0;
  justify-content: space-between;
}

.contacts__item {
  display: flex;
  width: 569px;
  align-items: center;
}
